export var APIRequest = APIRequest || {};
((APIreq) => {
  // APIKey 객체
  JSONAPIKey = function (key) {
    if (!(key instanceof String)) throw new TypeError("");

    let APIKey = key;

    return Object.freeze({
      key: () => {
        return APIKey;
      },
    });
  };

  // API URL 요청에 필요한 키와 값을 저장하는 객체
  JSONAPIRequestParam = function (key, value) {
    let _key = key instanceof String ? key : key.toString();
    let _value = value;

    return Object.freeze({
      key: () => {
        return _key;
      },
      value: () => {
        return _value;
      },
      type: () => {
        return typeof _value;
      },
    });
  };

  // API URL 요청 시 이벤트 콜백함수를 저장하는 객체
  APIreq.JSONAPIRequestEvent = function () {
    let EventCallback = {
      abort: null,
      error: null,
      load: null,
      loadstart: null,
      loadend: null,
      progress: null,
      readystatechange: null,
      timeout: null,
    };

    return Object.freeze({
      setEventCallback: function (event, callback) {
        if (event in EventCallback) EventCallback[event] = callback;
      },

      getEventCallback: function (event) {
        return event in EventCallback ? EventCallback[event] : null;
      },

      getEventList: function () {
        return Object.keys(EventCallback);
      },
    });
  };

  APIreq.JSONAPIRequest = function (APIKey, event = null) {
    let apiKey = APIreq.JSONAPIKey(APIKey);
    let req = new XMLHttpRequest();

    for (let eventType of event.getEventList()) {
      req.addEventListener(eventType, event.getEventCallback(eventType), false);
    }

    return Object.freeze({
      load: function (params, method, async = true) {
        let paramObj = JSON.parse(params);
        let paramList = [];

        for (let param of Object.keys(paramObj)) {
          paramList.push(
            new APIreq.JSONAPIRequestParam(param, paramObj[param])
          );
        }

        let apiUrl = addUrlParam(url, paramList);

        let data = null;
        req.onreadystatechange = function () {
          if (req.readyState == 4 && req.status == 200) data = req.responseText;
        };

        req.open(method, apiUrl, async);
        req.send(null);

        return data;
      },
    });
  };

  function addUrlParam(url, params) {
    if (params.length == 0) return url;

    url += "?";
    for (let i = 0; i < params.length; i++) {
      if (params[i].value == null) continue;

      url += params[i].key + "=" + encodeURIComponent(params[i].value) + "&";
    }

    url = url.slice(0, -1);
    return url;
  }
})(APIRequest);
